import React, { Component } from 'react'
import {Link} from "react-router-dom"
import GoBack from "../../components/GoBack/GoBack"
import Item from "./components/Item"
export default class Movie extends Component {

    constructor(){
        super()
        this.state={
            movies:[
                {id:0,name:"我和我的祖国"},
                {id:1,name:"大话西游"},
                {id:2,name:"咒怨"}
            ]
        }
    }

    goBack(){
        // console.log(this.props)
        // this.props.history.go(-1)
        this.props.history.goBack()
    }

    //跳转到详情页面
    toDetail(id){
        this.props.history.push("/movieDetail/"+id)
    }
    render() {
        const {movies}=this.state
        return (
            <div>
                <button onClick={()=>this.goBack()}>电影的返回</button>
                <GoBack></GoBack>
                <h1>movie</h1>

                <ul>
                    {
                        movies.map(item=>{
                        return <li onClick={()=>this.toDetail(item.id)} key={item.id}>{item.name}</li>
                        })
                    }
                </ul>


                <hr/>
                {
                    movies.map(item=>{
                        return (
                            <div key={item.id}>
                                <Link to={"/movieDetail/"+item.id}>{item.name}</Link>
                            </div>
                        )
                    })
                }

                <hr/>
                {
                    movies.map(item=>{
                        return <Item key={item.id} item={item}></Item>
                    })
                }



            </div>
        )
    }
}
